.ad{
  height: 310px;
  color: white;
  width: 100%;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 40px;
  border-radius: 10px;
  background-image: url(./black_friday.jpeg);
  background-position:center;
  background-size: cover;
  text-decoration: none;
  margin: auto auto auto auto;
  max-width: 1370px;
  min-width: 500px;
}

.whitelogo{
  width: 130px;
  position: relative;
  left: 0;
  top: 25%;
  transform: translate(0%, 0%);
  display: block;
  margin: auto;
}

.inside_text{
  color: white;
  position: relative;
  left: 68%;
  top: -20%;
  transform: translate(0%, 0%);
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
  font-weight: bold;
  text-decoration: none;
  margin-right: 70%;
}

.prosfores{
  width: 140px;
  height: 34px;
  background: #f7963b;
  color: white;
  border: 1px;
  border-radius: 4px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 13px;
  position: relative;
  left: 68%;
  top: -25%;
  transform: translate(0%, 0%);
}

.black{
  color: white;
  position: relative;
  left: 3%;
  top: -60%;
  transform: translate(0%, 0%);
  font-family: Arial, Helvetica, sans-serif;
  font-size: 115%;
  font-weight: bold;
}

.advertise{
  margin-top: 65px;
  margin-bottom: 50px;
  margin-left: 14%;
  margin-right: 14%;
}
