.header{
  margin-left: 14%;
  margin-right: 14%;
}

.head{
  margin: auto auto auto auto;
  max-width: 1370px;
  min-width: 500px;
}

.logo{
  margin-top: 80px;
  margin-bottom: 30px;
  margin-left: 40.5%;
}

.header_logo{
  width: 50%;
  max-width: 265px;
  min-width: 200px;
}

.search{
  width: 100%;
  max-width: 600px;
  height: 36px;
  box-sizing: border-box;
  border: 1px solid #d0d0d0;
  border-right: 0px;
  border-radius: 4px;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  float: left;
}

.search:focus{
  border: 1px solid #f68b24;
}

.searchbutton{
  width: 160px;
  height: 36px;
  background: #f68b24;
  color: white;
  border: 1px;
  border-radius: 4px;
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 15px;
}

.searchbutton:hover{
  background: #f57c0a;
}

.searchbutton:focus{
  background: #dc6f09;
}

.header_nav{
  margin-top: 40px;
  margin-left: 86%;
}

.searchbar{
  margin-left: 23%;
  margin-right: 20%;
}
